<template>
    <el-dialog
            :close-on-click-modal="false"
            :title="title"
            :visible="show"
            @close="closeDialog"
            append-to-body
            top="50px"
            width="80%"
    >
        <el-table
                :data="list"
                :header-cell-style="{background:'#e9eff8'}"
                @row-click="seeFunction"
                border
                size="mini"
                v-loading="loading"
        >
            <el-table-column align="center" label="状态" prop="caseStatus"/>
            <el-table-column align="center" label="部门" prop="enforceName" show-overflow-tooltip/>
            <el-table-column align="center" label="案发时间" prop="crimeTime">
                <template slot-scope="scope">{{scope.row.crimeTime | formatDate}}</template>
            </el-table-column>
            <el-table-column align="center" label="违反内容" prop="reasonName" show-overflow-tooltip/>
            <el-table-column align="center" label="决定书编号" prop="pmDecisionCode"/>
            <el-table-column align="center" label="当事人" prop="party" show-overflow-tooltip width="80"/>
            <el-table-column align="center" label="处罚金额" prop="decisionContent" width="80"/>
            <el-table-column align="center" label="缴款时间" prop="performanceTime">
                <template slot-scope="scope">{{scope.row.performanceTime | formatDate}}</template>
            </el-table-column>
            <el-table-column align="center" label="操作" width="50">
                <template slot-scope="scope">
                    <el-tooltip class="item" content="查看" effect="dark" placement="top-end">
                        <el-link
                                @click.stop="seeFunction(scope.row)"
                                class="el-icon-view btn-link"
                                type="success"
                        >
                            <i></i>
                        </el-link>
                    </el-tooltip>
                </template>
            </el-table-column>
        </el-table>

        <!-- 查看 -->
        <!-- <seeContent :editId='rowObj.editId' :show.sync='rowObj.show' :title='rowObj.title' :datarow='datarow'></seeContent> -->
        <see :datarow="datarow" :id="rowObj.editId" :show.sync="rowObj.show" :title="rowObj.title"></see>
    </el-dialog>
</template>

<script>
    import {formatDate, YESORNO} from "../../../until/wordBook";
    import {apiCaseOffereeGet} from "../../../api/case";
    import seeContent from "./seeContent";
    import see from "../easyCase/component/formItem/see";

    export default {
        name: "gettable",
        props: {
            editId: {
                type: String,
                default: ""
            },
            show: {
                type: Boolean,
                default: false
            },
            title: {
                type: String,
                default: ""
            }
        },
        components: {seeContent, see},
        data() {
            return {
                loading: true,
                rowObj: {
                    show: false,
                    title: "",
                    editId: null
                },
                datarow: {},
                list: [],
                tabPosition: "left"
            };
        },
        methods: {
            closeDialog() {
                this.$emit("update:show", false);
            },
            seeFunction(row) {
                this.rowObj.show = true;
                this.rowObj.title = "查看案件信息";
                this.rowObj.editId = row.scId;
            },
            //弹框弹出时获取data给form
            initPage() {
                apiCaseOffereeGet(this.editId).then(res => {
                    this.list = res.data;
                    this.loading = false;
                });
            }
        },
        filters: {
            YESORNO(val) {
                return YESORNO[val];
            },
            formatDate(val) {
                return formatDate(val, "yyyy-MM-dd");
            }
        },

        //监听，如果show为true,则执行initPage()
        watch: {
            show(value) {
                if (value) {
                    this.initPage();
                }
            }
        }
    };
</script>

<style lang="less" scoped>
    .nothing {
        width: 100%;
        height: 100px;
        text-align: center;
        line-height: 100px;
        font-weight: bold;
    }

    .concss {
        font-weight: bold;
    }

    .fontcss {
        font-family: "Microsoft YaHei", "微软雅黑", "Helvetica Neue", Helvetica,
        "PingFang SC", "Hiragino Sans GB", Arial, sans-serif;
        color: #333;
        font-weight: bold;
        //  display: inline-block;min-width: 100px;
        // border-radius: 5px;
        // padding:0 10px;
        // border: 1px dashed #C5C5C5
    }

    .detail-block {
        font-size:  @fontSize + 4;

        h3 {
            text-align: center;
        }

        .enforceCode {
            float: right;
        }

        .enforceRight-block {
            padding: 10px 20px 0 20px;

            .enforceRight {
                text-indent: 2em;
                margin: 10px 0;
            }
        }

        .isDisable {
            padding: 0 20px;
            font-size:  @fontSize ;
            color: #888;
        }
    }

    .btn-link {
        margin: 0 10px;
    }
</style>
