<template>
    <el-dialog :close-on-click-modal='false' :title="title" :visible="show" @close="closeDialog" append-to-body
               top="20px" width="900px">
        <table class="gridTable" v-loading="loading">

            <tr>
                <th>案件名称</th>
                <td>
                    <span>{{datarow.caseName}}</span>
                </td>
                <th>案发时间：</th>
                <td>
                    <span>{{datarow.crimeTime}}</span>
                </td>
            </tr>
            <tr>
                <th>案发地点：</th>
                <td>
                    <span>{{datarow.crimeLocal}}</span>
                </td>
                <th>发现时间：</th>
                <td>
                    <span class="fontcss"> {{datarow.discoveryTime}}</span>
                </td>
            </tr>
            <tr>
                <th>街道办：</th>
                <td colspan="4">
                    <span class="fontcss">{{datarow.streetOffice}}</span>
                </td>
            </tr>


            <!-- 单 -->
            <tr>
                <th>其他信息：</th>
                <td colspan="4">
                    <span class="fontcss">{{datarow.remarks}}</span>
                </td>
            </tr>
            <!-- 双 -->
            <tr>
                <th>决定书日期：</th>
                <td>
                    <span>{{datarow.discoveryTime}}</span>
                </td>
                <th>公示日期：</th>
                <td>
                    <span>{{datarow.discoveryTime}}</span>
                </td>

            </tr>


        </table>

    </el-dialog>

</template>

<script>
    import {formatDate, YESORNO} from "../../../until/wordBook";

    export default {
        name: "seeContent",
        props: {
            datarow: {
                type: Object,
                default: {}
            },
            editId: {
                type: String,
                default: ''
            },
            show: {
                type: Boolean,
                default: false
            },
            title: {
                type: String,
                default: ""
            },

        },
        data() {
            return {
                list: [],
                tabPosition: 'left'
            }
        },
        methods: {
            closeDialog() {
                this.$emit('update:show', false)
            },

            //弹框弹出时获取data给form
            initPage() {

            }
        },
        filters: {
            YESORNO(val) {
                return YESORNO[val]
            },
            formatDate(val) {
                return formatDate(val, 'yyyy-MM-dd')
            }
        },

        //监听，如果show为true,则执行initPage()
        watch: {
            'show'(value) {
                if (value) {
                    this.initPage();

                }
            }
        },


    }
</script>

<style lang="less" scoped>
    .nothing {
        width: 100%;
        height: 100px;
        text-align: center;
        line-height: 100px;
        font-weight: bold
    }

    .concss {
        font-weight: bold
    }

    .fontcss {
        font-family: "Microsoft YaHei", "微软雅黑", "Helvetica Neue", Helvetica, "PingFang SC", "Hiragino Sans GB", Arial, sans-serif;
        color: #333;
        font-weight: bold;
        //  display: inline-block;min-width: 100px;
        // border-radius: 5px;
        // padding:0 10px;
        // border: 1px dashed #C5C5C5
    }

    .detail-block {
        font-size:  @fontSize + 4;

        h3 {
            text-align: center;
        }

        .enforceCode {
            float: right;
        }

        .enforceRight-block {
            padding: 10px 20px 0 20px;

            .enforceRight {
                text-indent: 2em;
                margin: 10px 0;
            }
        }

        .isDisable {
            padding: 0 20px;
            font-size:  @fontSize ;
            color: #888;
        }
    }
</style>
