<template>
    <div>
        <el-dialog
                :close-on-click-modal="false"
                :visible="show"
                @close="closeDialog"
                append-to-body
                center
                title="案件详情"
                top="50px"
                width="1080px"
        >
            <el-row :gutter="10">
                <el-col :span="16">
                    <el-card class="box-card" style="position: relative">
                        <div class="status">{{form.caseStatus}}</div>
                        <table class="gridTable" v-if="form">
                            <tr>
                                <td class="label">案件来源：</td>
                                <td>{{caseSource(form.caseSource)}}</td>
                                <td class="label">案件来源编号：</td>
                                <td>{{form.caseSourceNum}}</td>
                            </tr>
                            <tbody v-for="data in casePartyList" :key="data.partyId">
                                <tr>
                                    <td class="label">当事人类型：</td>
                                    <td>{{data.partyType == 1?'个人':(data.partyType == 2 ? '单位' : '个体户')}}</td>
                                    <td class="label">当事人名称：</td>
                                    <td>{{data.party}}</td>
                                </tr>
                                <tr>
                                    <td class="label">当事人证件类型：</td>
                                    <td v-if="data.partyType == 1">{{idCardNum(data.partyCertificateType) }}</td>
                                    <td v-if="data.partyType == 2">{{comNum(data.partyCertificateType)}}</td>
                                    <td v-if="data.partyType == 3">{{fzNum(data.partyCertificateType)}}</td>
                                    <td class="label">当事人证照号码：</td>
                                    <td :colspan="data.partyType == 2 ? 1 : 3">{{data.partyIdcard}}</td>
                                </tr>
                                <tr>
                                    <td class="label" v-if="data.partyType == 2">责任人：</td>
                                    <td v-if="data.partyType == 2">{{ data.chargeLegalType == 1 ? '法人' : '负责人'}}</td>
                                    <td class="label" v-if="data.partyType == 2">{{data.chargeLegalType == 1?'法人':'负责人'}}：</td>
                                    <td v-if="data.partyType == 2">{{ data.partyType == 2 ? data.legalPerson : ''}}</td>

                                </tr>
                                <tr>
                                    <td class="label">当事人联系电话：</td>
                                    <td >{{data.partyTel}}</td>
                                    <td class="label">当事人联系地址：</td>
                                    <td colspan="3">{{data.partyAddress}}</td>
                                </tr>
                            </tbody>

                            <tr>
                                <td class="label">案发地点：</td>
                                <td colspan="3">{{form.crimeLocal}}</td>
                            </tr>
                            <tr>
                                <td class="label">违法行为发现时间：</td>
                                <td colspan="3">{{form.crimeTime | formatDate}}</td>
                            </tr>
                            <tr>
                                <td class="label">执法领域：</td>
                                <td>{{form.enforceName}}</td>
                                <td class="label">违法行为类型：</td>
                                <td>{{form.illegalTypesName}}</td>
                            </tr>
                            <tr>
                                <td class="label">违法行为：</td>
                                <td colspan="3">{{form.reasonName}}</td>
                            </tr>
                            <tr>
                                <td class="label">法律法规规章规定：</td>
                                <td colspan="3">{{form.violationRegulationsContent}}</td>
                            </tr>
                            <tr>
                                <td class="label">法律责任适用条款：</td>
                                <td colspan="3">{{form.accordingRegulationsContent}}</td>
                            </tr>


                            <tr>
                                <td class="label">当场处罚决定书编号：</td>
                                <td colspan="3">{{form.pmDecisionCode}}</td>
                            </tr>
                            <tr>
                                <td class="label">责令改正：</td>
                                <td>{{form.correctionMethod == 1 ? '立即':''}}</td>
                                <td class="label">处罚类型：</td>
                                <td>
                                    <span class="punish-css" v-if="form.isProcessingWarning == 1">警告</span>
                                    <span class="punish-css" v-if="form.isProcessingPunish == 1">罚款</span>
                                </td>
                            </tr>
                            <tr v-if="form.isProcessingPunish">
                                <td class="label">罚款金额：</td>
                                <td>{{form.penaltyAmount}}</td>
                                <td class="label">罚款方式：</td>
                                <td>{{form.paymentMethod == 1 ? '当场':(form.paymentMethod == 2 ? '银行':'')}}</td>
                            </tr>
                            <tr v-if="form.paymentMethod == 1">
                                <td class="label">定额票据编号：</td>
                                <td colspan="3">
                                    {{quotaInvoice | quotaNum}}
                                </td>
                            </tr>
                            <tr v-if="form.paymentMethod == 2">
                                <td class="label">缴款银行：</td>
                                <td>{{form.payingBank == 1 ? '建设银行':(form.payingBank == 2 ? '工商银行' : '')}}</td>
                                <td class="label">缴款单据编号：</td>
                                <td>{{form.paymentReceiptNumber}}</td>
                            </tr>
                            <tr>
                                <td class="label">交款时间：</td>
                                <td colspan="3">{{form.performanceTime| formatDate2}}</td>
                            </tr>
                            <tr>
                                <td class="label">办案部门：</td>
                                <td>{{form.handlingDepartmentName}}</td>
                                <td class="label">主办人：</td>
                                <td>{{form.investigatorsName}}</td>

                            </tr>
                            <tr>
                                <td class="label">协办人：</td>
                                <td colspan="3">{{form.coInvestigatorsName}}</td>

                            </tr>
                        </table>
                    </el-card>
                </el-col>
                <el-col :span="8">
                    <el-card :key="item.docName" class="box-card" v-for="item in documentList">
                        <div class="clearfix" slot="header">
                            <span>{{item.docName}}</span>
                        </div>
                        <div class="no-block" v-if="filterList(item.docTypeId).length < 1">暂无文书</div>
                        <div :key="data.value" class="text item" v-for="data in filterList(item.docTypeId)">
                            <i class="el-icon-tickets"></i>
                            {{data.fileName}}
                            <div class="doc-css">
                                <el-tooltip :open-delay="500" content="查看当前文书" placement="top">
                                    <el-link @click.stop="showImg(data)" icon="el-icon-search" type="primary"></el-link>
                                </el-tooltip>
                                <el-tooltip :open-delay="500" class="download-doc" content="下载当前文书" placement="top">
                                    <el-link @click.stop="dowImg(data)" icon="el-icon-download"
                                             type="success"></el-link>
                                </el-tooltip>
                            </div>
                        </div>
                    </el-card>
                </el-col>
            </el-row>

            <span class="dialog-footer" slot="footer">
        <el-button @click="closeDialog" size="small" type="info">
          <i class="iconfont icon-back"></i>关 闭
        </el-button>
      </span>
        </el-dialog>
    </div>
</template>
<script>
    import {apiDocumentTypeSearch, apiFileDownLoad, apiSimpleCaseDetailGet, seeImgApi} from "../../../../../api/case";
    import {formatDate} from "../../../../../until/wordBook";

    export default {
        name: "edit",
        props: {
            show: {
                type: Boolean,
                default: false
            },
            id: {
                type: String,
                default: ""
            }
        },
        data() {
            return {
                form: {},
                documentList: [],
                certificatesType: [],
                quotaInvoice: [],
                casePartyList:[],
                selfEmployedWorker:[],
                caseSourceList: [],
                fileList: [],
                personalCertificatesType: [],

            };
        },
        computed: {
            filterList() {
                return function (o) {
                    return this.fileList.filter(i => {
                        return i.docTypeId == o;
                    });
                };
            },
        },
        methods: {
            dowImg(data) {
                apiFileDownLoad(data.fileId);
            },
            showImg(data) {
                window.open(seeImgApi + data.fileId);
            },
            closeDialog() {
                this.$emit("update:show", false);
            },
            //弹框弹出时获取data给form
            initPage() {
                apiSimpleCaseDetailGet(this.id).then(res => {
                    this.form = res.simpleCaseData;
                    this.fileList = res.fileData;
                    this.casePartyList = res.casePartyList;
                    this.quotaInvoice = res.quotaInvoice;
                });
            },
            idCardNum(val) {
                if(this.personalCertificatesType.find(i => {
                    return i.dictValue == val
                })){
                    return this.personalCertificatesType.find(i => {
                        return i.dictValue == val
                    }).dictName;
                }else {
                    return val
                }

            },
            comNum(val) {
                if (this.certificatesType.find(i => {
                    return i.dictValue == val
                })) {
                    return this.certificatesType.find(i => {
                        return i.dictValue == val
                    }).dictName;
                }else {
                    return val
                }

            },
            fzNum(val) {
                if (this.selfEmployedWorker.find(i => {
                    return i.dictValue == val
                })) {
                    return this.selfEmployedWorker.find(i => {
                        return i.dictValue == val
                    }).dictName;
                }else {
                    return val
                }

            },
            caseSource(value) {
                let obj = this.caseSourceList.find(i => {
                    return i.dictValue == value;
                });
                if (obj) {
                    return obj.dictName;
                } else {
                    return "";
                }
            }
        },
        filters: {
            formatDate(val) {
                return formatDate(val, "yyyy-MM-dd hh:mm");
            },
            quotaNum(val) {
                let arr = [];
                for (let key in val) {
                    val[key].forEach(i => {
                        arr.push(i.quotaInvoiceNum)
                    });
                }
                return arr.join(',')
            },
            formatDate2(val) {
                return formatDate(val, "yyyy-MM-dd");
            }
        },
        created() {
            this.$until.workBookApi("caseSource").then(res => {
                this.caseSourceList = res;
            });
            this.$until.workBookApi('personalCertificatesType').then(res => {
                this.personalCertificatesType = res
            });
            this.$until.workBookApi('selfEmployedWorker').then(res => {
                this.selfEmployedWorker = res
            });
            this.$until.workBookApi('certificatesType').then(res => {
                this.certificatesType = res
            });
            apiDocumentTypeSearch({pageNum: 1, pageSize: 1000, caseType: "simple"}).then(res => {
                this.documentList = res.data;
            });
        },
        //监听，如果show为true,则执行initPage()
        watch: {
            show(value) {
                if (value) {
                    this.initPage();
                }
            }
        }
    };
</script>
<style lang='less' scoped>
    .alert_wrop {
        padding: 10px;
        display: flex;
        justify-content: space-between;
    }

    .status {
        position: absolute;
        top: 50px;
        right: 0;
        color: red;
        border: 3px solid red;
        font-size: 20px;
        transform: rotate(45deg);
        opacity: 0.2;
    }

    .selectwidth {
        width: 100%;
    }

    .box-card {
        margin: 10px 0;
        line-height: 2em;
    }

    .no-block {
        text-align: center;
        font-size: 20px;
        color: #cccccc;
        line-height: 60px;
    }

    table {
        .gridTable;
        border-collapse: collapse;
        width: 100%;

        td {
            width: 25%;

            &.label {
                width: 500px;
            }
        }
    }

    .punish-css {
        padding: 0 5px;
    }

    .doc-css {
        float: right;
    }

    .download-doc {
        margin-left: 5px;
    }
</style>


