<template>
    <div>
        <div class="wrop-search">
            <lj-search-from :searchObj="searchObj" @searchList="searchList"/>
        </div>
        <!-- 表格 -->
        <el-table :data="list" :header-cell-style="{background:'#e9eff8'}" :row-style="{cursor:'pointer'}"
                  @row-click="seeFunction" border size="mini" v-loading="loading">
            <el-table-column align="center" label="相对人名称" prop="offereeName"/>
            <el-table-column align="center" label="相对人类型" prop="offereeType" width="100">
                <template slot-scope="scope" v-if='scope.row.offereeType'>
                    {{scope.row.offereeType | offereeType}}
                </template>
            </el-table-column>
            <el-table-column align="center" label="相对人编码" prop="offereeCode"/>
            <el-table-column align="center" label="法人" prop="offereeLegal" width="130"/>
            <el-table-column align="center" label="联系电话" prop="offereeTel"/>
            <el-table-column align="center" label="操作" width="50">
                <template slot-scope="scope">
                    <el-tooltip class="item" content="查看" effect="dark" placement="top-end">
                        <el-link @click.stop="seeFunction(scope.row)" class="el-icon-view btn-link"
                                 type="success"><i></i></el-link>
                    </el-tooltip>
                </template>
            </el-table-column>

        </el-table>

        <lj-pagination :pageNum.sync="form.pageNum" :pageSize.sync="form.pageSize" :total="total"
                       @getData="getList"
                       class="page-css"/>

        <!-- 编辑&添加弹框 -->
        <change-edit :editId='seeObj.editId' :show.sync='seeObj.show' :title='seeObj.title'/>

    </div>
</template>

<script>
    import {apiCaseOffereeSearch} from "../../../api/case";
    import LjPagination from '../../../components/LjPagination'
    import LjSearchFrom from '../../../components/LjSearchForm'
    import {formatDate, IFSEX} from "../../../until/wordBook";
    import changeEdit from "./gettable";

    export default {
        name: "relativePersonPage",
        data() {
            return {
                loading: true,
                //弹出框数据
                editObj: {
                    show: false,
                    title: '',
                    type: '',
                    editId: null
                },
                //查看数据
                seeObj: {
                    show: false,
                    title: '',
                    editId: null
                },

                //搜索组件数据
                searchObj: null,
                form: {
                    pageSize: 1000,
                    pageNum: 1,
                },
                total: 0,
                input: '',
                list: [],
                rowdata: {}
            }
        },
        components: {
            changeEdit, LjPagination, LjSearchFrom
        },

        methods: {
            //查询+搜索

            getList() {
                this.loading = true;
                apiCaseOffereeSearch(this.form).then(res => {
                    this.total = res.total;
                    this.list = res.data;
                    this.loading = false;
                })
            },
            //查看
            seeFunction(row) {
                this.seeObj.show = true;
                this.seeObj.title = '查看案件信息';
                this.seeObj.editId = row.offereeCode;
            },

            //搜索
            searchList(data) {  //data就是接收的this.form数据
                this.loading = true;
                data.pageSize = this.form.pageSize;
                this.form = data;
                apiCaseOffereeSearch(data).then(res => {
                    this.list = res.data;
                    this.total = res.total;
                    this.loading = false;

                })

            },

        },
        filters: {
            ifsex(val) {
                return IFSEX[val]
            },
            formatDate(value) {
                return formatDate(value, 'yyyy-MM-dd hh:mm:ss')
            },
            offereeType(val) {
                return val == 1 ? '自然人' : (val == 2 ? '法人' : (val == 3 ? '个体户' : '无'))
            }

        },
        computed: {},
        mounted() {

        },

        created() {
            this.getList();
        },
    }
</script>

<style scoped>
    .wrop-search {
        margin-bottom: 10px
    }

    .btn-link {
        margin: 0 10px
    }

    .page-css {
        margin-top: 10px
    }
</style>
